<template>
<div id="background" :style="style">
  <div :style="layer1" class="layer">
    <img src="../assets/scene/background/temple1.jpg" alt="">
  </div>
  <div :style="layer2" class="layer">
    <img v-for="(cloud, cloudKey) in clouds" :style="cloud" src="../assets/scene/background/cloud1.png" alt="">
    <img v-for="(cloud2, cloud2Key) in clouds2" :style="cloud2" src="../assets/scene/background/cloud2.png" alt="">

  </div>
</div>
</template>

<script>
export default {
  props: {
    height: Number,
    width: Number,
    rooms: Array,
  },
  data() {
    var clouds = [];
    var clouds2 = [];
    for (var i = 0; i < this.randomIntFromInterval(3 * this.rooms.length, this.rooms.length); i++) {
      clouds.push({
        position: 'absolute',
        left: this.randomIntFromInterval() + 'px',
        top: this.randomIntFromInterval(400) + 'px',
        width: this.randomIntFromInterval(500, 300) + 'px'
      });
      clouds2.push({
        position: 'absolute',
        left: this.randomIntFromInterval() + 'px',
        top: this.randomIntFromInterval(400) + 'px',
        width: this.randomIntFromInterval(300, 150) + 'px'
      });
    }
    return {
      layer1: {},
      layer2: {},
      clouds: clouds,
      clouds2: clouds2,
      style: {
        width: this.width ? `${this.width}px` : '100%',
        position: 'absolute',
        left: '0'
      }
    }
  },
  methods: {
    getRandomInt: function(max) {
      var maximum = max || this.width;
      return Math.floor(Math.random() * Math.floor(maximum));
    },
    randomIntFromInterval: function(max, min) {
      var maximum = max || this.width;
      var minimum = min || 0;
      return Math.floor(Math.random() * (maximum - minimum + 1) + minimum);
    },
    slide(left) {
      this.style.left = left * 0.1 + "px";
      this.layer1.left = left * .05 + "px";
      this.layer2.left = left * .1 + "px";
    },
    init: function() {},
  },
  mounted() {
    this.$emit('backgroundCreated')
  }
}
</script>
<style>
#background {
  background-color: #52bf74;
  width: 100%;
  height: 100%;
  z-index: -3;
}

.layer {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;

  left: 0;

}
</style>



