<template>
    <div id="foreground" :style="style" >
      <div :style="layer1" class="layer">
      </div>
      <div :style="layer2"  class="layer">

        <!-- <img src="../assets/scene/foreground/grassfront.png" alt=""> -->
      </div>
    </div>
</template>

<script>

  export default {
    props: {
      height: Number,
      width: Number,
    },

    data () {
      return {
        layer2:{backgroundImage: 'url(' + require('../assets/scene/foreground/grassfront.png')+')',  marginLeft: '-35%', backgroundSize: '23%',width:this.width*4+"px"},
        layer1:{backgroundImage: 'url(' + require('../assets/scene/foreground/grassback.png')+')',bottom:"12vh",   marginLeft: '-20%', backgroundSize: '12%' ,width:this.width*4+"px"},
        style: {
          width: this.width ? `${this.width}px` : '100%',
          position: 'absolute',
          left: '0',
          bottom: 0,
          top: 0
        }
      }
    },
    methods: {
  // Used to focus the input from the parent
  slide (left) {
    console.log(left)
    this.style.left=left+"px";
    this.layer1.left=left*.1+"px";
    this.layer2.left=left*1.5+"px";
  }
},
    mounted () {
      this.$emit('foregroundCreated')
    }
  }
</script>
<style>
#foreground {
  width: 100%;
    height: 100%;
    z-index: -1;
}
.layer{
  position: absolute;
  top: 0;
  bottom:0;
  right:0;
  left:0;
  background-repeat: repeat-x;
  background-position: bottom;

}
</style>



