<template>
	<div id="home">
    <div id="screen">
      <img src="../assets/ui/2x/SQlogo_green@2x.png" width="513" height="337" />
       <b-button id="start" class="startbt" variant="primary" v-on:click="startGame">Démarrer</b-button>
    </div>
	</div>
</template>

<script>


export default {
  name: 'Home',
  data () {
    return {
    	msg: '',
    }
  },
  methods: {
    startGame: function() {
      this.$router.push( '/game' );
    }
  },
  created: function() {
  	
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

#screen {
  position: absolute;
  top:15%;
  left: 50%;
  transform: translateX(-50%);
  width: 513px;
  max-width: 100%;
  img { 
    max-width: 100%;
    height: auto;
    display: block; 
  }
  button.startbt {
    background: transparent url('../assets/ui/1x/ICO_ext_play.png') no-repeat top left;
    background-size: 108px 108px;
    width: 108px;
    height: 108px;
    margin-left: 50%;
    transform: translateX(-50%);
    border: 0;
    border-radius: 0;
    text-indent: -9999px;
    overflow: hidden;
  }
}
	
</style>



